.controlWrap {
    left: 50%;
    opacity: 0;
    bottom: -85%;
    border: none;
    color: #c4c4c4;
    font-size: 32px;
    background-color: transparent;

    position: absolute;
    transition: color .6s, transform 1s, opacity 1s;
    &:hover, &:focus {
        outline: none;
        color: #414141; }
    &.mLeft {
        transform: translateX(-200%) translateY(-50%); }
    &.mRight {
        transform: translateX(150%) translateY(-50%); }
    &.active {
        opacity: 1;
        transform: translate(-50%, -50%); } }
