.form-wrap {
    width: 500px;
    position: relative;
    // background-color: #fafafa
    padding: 2.2rem 2rem 1.8rem;
    margin: 20vh auto;
    border-radius: 3px;
    // border: 1px solid #ddd
    transition: border-color .3s ease-in-out, transform 2s;
    @include mq(xs) {
        width: 100%;
        margin: auto; }
    &:hover {
        border-color: #444; } }

.form-group {

    >label {
        display: block;
        margin-bottom: 10px; }
    .input-wrap {
        position: relative; }
    input {
        width: 100%;
        border: none;
        font-family: inherit;
        padding: 10px 30px 10px 0;
        background-color: transparent;
        border-bottom: 1px solid #eaeaea;
        transition: border-color .3s ease-in-out;
        &:focus {
            border-bottom-color: #444; } }
    .submit-btn {
        right: 0;
        top: 50%;
        border: none;
        color: #c1c1c1;
        font-size: 18px;
        position: absolute;
        transform: translateY(-48%);
        background-color: transparent; } }

.alerts {
    width: 500px;
    margin: auto;
    padding: 15px;
    font-size: 16px;
    &.success {
        color: #11a111; }
    &.error {
        color: #e34c44; } }

.form-wrap.active {
    transform: translateY(70%);
    @include mq(xs) {
        transform: translateY(150%); }
    @include mqMax(344) {
        transform: translateY(110%); } }

.alert {
    opacity: 0;
    height: 20px;
    display: block;
    color: #eb633d;
    font-size: 12px;
    margin-top: 5px;
    letter-spacing: 2px;
    transition: transform 2s, opacity 1s ease-in-out;
    transform: translateY(20px);
    @include mq(xs) {
        font-size: 10px; }
    &.shown {
        opacity: 1;
        transform: translateY(0); } }

.user-headset {
    left: 50%;
    width: 100%;
    bottom: -20%;
    text-align: center;
    transform: translate(-50%, -50%);
    color: #c4c4c4;
    font-size: 1rem;
    position: absolute;
    @include mq(xxs) {
        font-size: 12px; } }

.motivate-wrap {
    display: flex;
    justify-content: center;
    .motivate-btn {
        z-index: 2;
        outline: none;
        opacity: 0.78;
        color: #fafafa;
        font-size: 1.5rem;
        border-radius: 2rem;
        padding: 10px 22px;
        transition: opacity .6s;
        background-color: #ea5a52;
        border: 1px solid transparent;
        &.hidden {
            opacity: 0;
            pointer-events: none; }
        &:not(.hidden):hover {
            opacity: 1; } } }

.loading {
    opacity: 0;
    padding: 1.5rem 0;
    transition: opacity .6s ease-in-out;
    &.active {
        opacity: 1; } }

.gooey {
  position: absolute;
  opacity: 0.3;
  top: 50%;
  left: 50%;
  width: 142px;
  height: 40px;
  margin: -20px 0 0 -71px;
  background: white;
  filter: contrast(20);
  .dot {
    position: absolute;
    width: 16px;
    height: 16px;
    top: 12px;
    left: 15px;
    filter: blur(4px);
    background: #444;
    border-radius: 50%;
    transform: translateX(0);
    animation: dot 2.8s infinite; }
  .dots {
    transform: translateX(0);
    margin-top: 12px;
    margin-left: 31px;
    animation: dots 2.8s infinite;
    span {
      display: block;
      float: left;
      width: 16px;
      height: 16px;
      margin-left: 16px;
      filter: blur(4px);
      background: #444;
      border-radius: 50%; } } }

@keyframes dot {
  50% {
    transform: translateX(96px); } }
@keyframes dots {
  50% {
    transform: translateX(-31px); } }
