.copyrights {
    text-align: center;
    left: 50%;
    bottom: 30px;
    min-width: 250px;
    position: absolute;
    transform: translateX(-50%);
    @include mq(sm) {
        width: 100%;
        bottom: 20px;
        position: absolute; }

    >span {
        padding: 5px 10px;
        border-radius: 3px;
        display: inline-block;
        background-color: rgba(255,255,255,.5); } }
